:root {
    --primary-color: #000000;
    --secondary-color: #ffffff;
    --tertiary-color: #808080;
  }

.footer {
    background-color: #000000;
    list-style-type: none;
    padding: 20px 30px;
    padding-left: 40%;
    margin-left: -6%;
}
.footerlink {
    display: inline;
    flex-wrap: wrap;
    align-items: center;
    list-style: none;
    float: none;
    margin: 0 30px;
    justify-content: center;
}
a {
    text-decoration: none;
    padding: 20px;
    background-color: transparent;
    transform-origin: center;
    list-style-type: none;
}
a:hover {
    text-decoration: none;
    padding: 20px;
    background-color: transparent;
    transform-origin: center;
    list-style-type: none;
}
.icon{
    font-size: 40px;
    text-decoration: none;
    color: rgba(2251,153,2, 0.9);
    transform-origin: center;
    list-style-type: none;
    cursor: pointer;   
}
.icon:hover{
    font-size: 40px;
    text-decoration: none;
    color: var(--tertiary-color);
    transform-origin: center;
    list-style-type: none;
}
body {
    background-color: var(--primary-color);
}
